<template>
    <div id="app">
        <div class="wrap_res">
            <div class="window_con">
                <div class="btn_close" @click="closeWindow">
                    <img src="@/assets/icon_close@2x.png" />
                </div>
                <div class="responder" v-if="status==='num'">
                    <div class="number">{{number}}</div>
                    <div class="tip">{{number}}秒后在手写板上按“确定”键</div>
                </div>
                <div class="loading" v-else-if="status==='loading'">
                    <div class="tip">正在等待学生抢答...</div>
                    <div class="btn_close" @click="closeWindow">
                        <img src="@/assets/icon_close@2x.png" />
                    </div>
                </div>
                <div class="topThree" v-else>
                    <div class="topList">
                        <div class="student tiny">
                            <template v-if="topThree.length>1">
                                <div class="avatar"><img :src="topThree[1].avatar" /><img class="crown" src="@/assets/no2@2x.png"/></div>
                                <div class="nickname" v-animate-css="'fadeIn'">{{topThree[1].name}}</div>
                                <div class="usetime" v-animate-css="'fadeIn'">{{topThree[1].time}}</div>
                            </template>

                        </div>
                        <div class="student">
                                <template v-if="topThree.length>0">
                                <div class="avatar"><img :src="topThree[0].avatar" /><img class="crown" src="@/assets/no1@2x.png"/></div>
                                <div class="nickname">{{topThree[0].name}}</div>
                                <div class="usetime">{{topThree[0].time}}</div>
                            </template>

                        </div>
                        <div class="student tiny">
                                <template v-if="topThree.length>2">
                                <div class="avatar"><img :src="topThree[2].avatar" /><img class="crown" src="@/assets/no3@2x.png"/></div>
                                <div class="nickname" v-animate-css="'fadeIn'">{{topThree[2].name}}</div>
                                <div class="usetime" v-animate-css="'fadeIn'">{{topThree[2].time}}</div>
                            </template>

                        </div>
                    </div>
                    <div class="btn_close" @click="closeWindow">
                        <img src="@/assets/icon_close@2x.png" />
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>
<script>
    import API from '@/assets/api.js'
    import utils from '@/assets/utils.js'
    import axios from 'axios'


    export default {
        name: 'App',
        data() {
            return {
                topThree: [],
                number: 3,
                status: "num",
                recordId: "",
                windowHeight: 0
            }
        },
        mounted() {
            //注册需要外部调用的方法
            window.setTimer = this.setTimer
            window.setLoading = this.setLoading
            window.setStudent = this.setStudent
            this.recordId = utils.getUrlPrams("id")
            console.log(window.innerHeight)
            this.windowHeight = window.innerHeight
            if (this.recordId) {
                this.status = "top"
                this.loadTop()
            }
            //获取url参数
        },
        methods: {
            setTimer(num) {
                this.number = num
                this.status = "num"
            },
            setLoading() {
                this.status = "loading"
            },
            setStudent(avatar, name, time) {
                this.status = "top"
                let stu = {
                    "avatar": avatar,
                    "name": name,
                    "time": time,
                }
                this.topThree.push(stu)
            },
            loadTop() {
                let that = this
                let id = this.recordId
                axios.get(API.api_tl + 'api/selOptionStatistic', {
                    params: {
                        // taskIds: that.taskIds.join(",")
                        interactRecordId: id
                    }
                }).then(function (res) {
                    let list = res.data.data.optionList
                    for (const stu of list) {
                        let student = { "avatar": stu.imgUrl, "name": stu.stuName }
                        that.topThree.push(student)
                    }
                    console.log(that.topThree)
                }).catch(function (error) {
                    console.log(error);
                });
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowResponder",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },

        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }


    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
</style>
<style scoped>
    .wrap_res {
        display: flex;
        justify-content: space-around;
        align-items: center;
        overflow: hidden;
        height: 100vh;
    }

    .window_con {
        width: 4.8rem;
        height: 3.7rem;
        box-sizing: border-box;
        position: relative;
    }

    .responder .number {
        padding-top: 0.15rem;
        font-size: 2.5rem;
        line-height: 3rem;
        color: #00AAFF;
    }

    .responder .tip {
        font-size: 0.18rem;
        color: #2D4A66;
        margin-top: 0.05rem;
    }

    .loading{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .loading .tip {
        color: #2D4A66;
        font-size: 0.28rem;
    }


    @-webkit-keyframes fadeIn {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    @-webkit-keyframes bounceIn {

        from,
        20%,
        40%,
        60%,
        80%,
        to {
            -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
            animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        }

        0% {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }

        50% {
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }

        to {
            opacity: 1;
            -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
        }
    }

    .topList {
        display: flex;
        justify-content: space-around;
        align-items: baseline;
        padding: 0 0.2rem;
    }

    .topList .student {
        width: 1.5rem;
    }

    .topList .student .crown{
        width: 0.45rem;
        bottom: 1.5rem;
        margin-left: -0.225rem;
        left: 50%;
        position: absolute;
    }

    .topList .student.tiny {
        width: 1rem;
    }

    .topList .student.tiny .crown{
        bottom: 1rem;
    }

    .topList .avatar {
        width: 1.5rem;
        height: 1.5rem;
        transition: all 0.3s linear;
        animation: 0.3s bounceIn;
        padding-top: 0.95rem;
        padding-bottom: 0.1rem;
        position: relative;
    }

    .topList .tiny .avatar {
        width: 1rem;
        height: 1rem;
    }

    .topList .avatar img {
        width: 100%;
        border-radius: 50%;
    }

    .topList .nickname {
        font-size: 0.16rem;
        color: #2D4A66;
        line-height: 0.23rem;
        margin-bottom: 0.05rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .topList .usetime {
        font-size: 0.16rem;
        line-height: 0.23rem;
        color: #00AAFF;
    }
</style>